import React, { useEffect, useState, useContext } from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import Qualification from './Qualification';
import Education from './Education';
import CardWhite from '../elements/CardWhite';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import Loading from '../elements/Loading';
import FirebaseService from '../../app/services/firebase/firebase-service';
import DeleteEducationModal from './DeleteEducationModal';
import DeleteQualificationModal from './DeleteQualificationModal';
import { TextContext } from '../../app/providers';

const ViewResume = ({ goToResume }) => {
  const [education, setEducation] = useState([]);
  const [course, setCourse] = useState(null);
  const [institution, setInstitution] = useState(null);
  const [qualification, setQualification] = useState([]);
  const [company, setCompany] = useState(null);
  const [position, setPosition] = useState(null);
  const [isLoading, setIsLoading] = useState([]);
  const [isEducationOpen, setIsEducationOpen] = useState(false);
  const [isQualificationOpen, setIsQualificationOpen] = useState(false);
  const [educationIndex, setEducationIndex] = useState(null);
  const [qualificationIndex, setQualificationIndex] = useState(null);
  const { setText } = useContext(TextContext);

  const deleteEducation = (index, delCourse, delInstitution) => {
    setEducationIndex(index);
    setCourse(delCourse);
    setInstitution(delInstitution);

    if (delCourse && delInstitution) {
      setIsEducationOpen(true);
    }
  };

  const deleteQualification = (index, delCompany, delPosition) => {
    setQualificationIndex(index);
    setCompany(delCompany);
    setPosition(delPosition);

    if (delCompany && delPosition) {
      setIsQualificationOpen(true);
    }
  };

  const handleEducationDelete = async () => {
    setIsLoading(true);
    const array = education;
    array.splice(educationIndex, 1);
    let message = '';
    try {
      await FirebaseService.postStudentResume(qualification, array);
      message = 'Qualificação educacional apagada com sucesso';
    } catch (err) {
      message = 'Erro ao tentar apagar, por favor tente novamente';
    } finally {
      toast.info(message, {
        position: toast.POSITION.BOTTOM_RIGHT
      });
      setText(message);
      setIsLoading(false);
      setIsEducationOpen(false);
    }
  };

  const handleQualificationDelete = async () => {
    setIsLoading(true);
    const array = qualification;
    array.splice(qualificationIndex, 1);
    let message = '';
    try {
      await FirebaseService.postStudentResume(array, education);
      message = 'Qualificação educacional apagada com sucesso';
    } catch (err) {
      message = 'Erro ao tentar apagar, por favor tente novamente';
    } finally {
      toast.info(message, {
        position: toast.POSITION.BOTTOM_RIGHT
      });
      setText(message);
      setIsLoading(false);
      setIsQualificationOpen(false);
    }
  };

  useEffect(() => {
    const loadResume = async () => {
      setIsLoading(true);
      const resume = await FirebaseService.getStudentResume();
      if (resume) {
        setEducation(resume.education);
        setQualification(resume.qualification);
      }
      setIsLoading(false);
    };
    loadResume();
  }, []);


  return (
    <CardWhite>
      {isEducationOpen && (
        <DeleteEducationModal
          isOpen={isEducationOpen}
          onClose={() => {
            setIsEducationOpen(false);
          }}
          course={course}
          institution={institution}
          deleteCallback={handleEducationDelete}
          cancelCallback={() => {
            setIsEducationOpen(false);
          }}
        />
      )}
      {isQualificationOpen && (
        <DeleteQualificationModal
          isOpen={isQualificationOpen}
          onClose={() => {
            setIsQualificationOpen(false);
          }}
          company={company}
          position={position}
          deleteCallback={handleQualificationDelete}
          cancelCallback={() => {
            setIsQualificationOpen(false);
          }}
        />
      )}
      <div className="resume__header">
        <div className="resume__header__title">
          <div className="row">
            <div className="col-lg-12">
              <h1>Meu currículo</h1>
            </div>
          </div>
        </div>
      </div>
      {isLoading ? (
        <Loading />
      ) : (
          <>
            <div className="row">
              <div className="col-12">
                <div className="resume__body">
                  <div className="resume__body__title">
                    <div className="row">
                      <div className="col-12">
                        <h2>Experiência profissional</h2>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-2">
                        <div className="resume__body__title__underline">
                          {/* underline */}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="resume__body__content">
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="resume__qualification">
                          {qualification && !qualification.length ? (
                            <div className="resume__empty">
                              Nenhuma qualificação profissional cadastrada
                          </div>
                          ) : (
                              qualification.map((item, index) => (
                                <Qualification
                                  company={item.company}
                                  position={item.position}
                                  index={index}
                                  deleteQualification={() => {
                                    deleteQualification(
                                      index,
                                      item.company,
                                      item.position
                                    );
                                  }}
                                  key={`${item.company}_${item.jobStartAt}`}
                                />
                              ))
                            )}
                          <ButtonWithVoice
                            customClass="form__button__next"
                            text="nova qualificação profissional"
                            action={() => {
                              goToResume(1);
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <div className="resume__body">
                  <div className="resume__body__title">
                    <div className="row">
                      <div className="col-12">
                        <h2>Experiência educacional</h2>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-2">
                        <div className="resume__body__title__underline">
                          {/* underline */}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="resume__body__content">
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="resume__education">
                          {education && !education.length ? (
                            <div className="resume__empty">
                              Nenhuma qualificação educacional cadastrada
                          </div>
                          ) : (
                              education.map((item, index) => (
                                <Education
                                  institution={item.institution}
                                  course={item.course}
                                  degree={item.degree}
                                  index={index}
                                  deleteEducation={() => {
                                    deleteEducation(
                                      index,
                                      item.course,
                                      item.institution
                                    );
                                  }}
                                  key={`${item.institution}_${item.educationStartAt}`}
                                />
                              ))
                            )}
                          <ButtonWithVoice
                            customClass="form__button__next"
                            text="nova qualificação educacional"
                            action={() => {
                              goToResume(2);
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </>
        )}
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnVisibilityChange
        draggable
        pauseOnHover
      />
    </CardWhite>
  );
};

export default ViewResume;
