import React, { useState, useContext, useEffect } from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import { navigate } from '@reach/router';
import CardWhite from '../elements/CardWhite';
import Loading from '../elements/Loading';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import InstructionsWithVoice from '../voice/InstructionsWithVoice';
import LoginService from '../../app/services/login/login-service';
import { WatsonContext } from '../../app/providers/WatsonProvider';
import { contentRoutes } from '../../app/utils/routes/index';
import { SpeechContext, TextContext } from '../../app/providers';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';


const LoginForm = ({ toggleFace, faceLogin }) => {
  const [email, setEmail] = useState('');
  const [senha, setSenha] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const { setWatson } = useContext(WatsonContext);
  const { setText } = useContext(TextContext);
  const [firstTrigger, setFirstTrigger] = useState(false);
  const speech = useContext(SpeechContext);

  const handleSubmit = async () => {
    try {
      setIsLoading(true);
      const response = await LoginService.loginUserWithEmail(email, senha);
      if (!response.err && response) {
        setText(`Olá, ${response.name}`);
        setWatson(response);
        navigate(contentRoutes.curriculo);
      } else {
        switch (response.err) {
          case 'auth/wrong-password':
            toast.info('SENHA INCORRETA', {
              position: toast.POSITION.TOP_RIGHT
            });
            setText('SENHA INCORRETA, por favor tente novamente');
            break;
          default:
            toast.info('OCORREU UM ERRO', {
              position: toast.POSITION.TOP_RIGHT
            });
            setText('OCORREU UM ERRO, por favor tente novamente');
            break;
        }
        setFirstTrigger(false);
      }
    } catch (err) {
      toast.info('OCORREU UM ERRO', {
        position: toast.POSITION.TOP_RIGHT
      });
      setText('OCORREU UM ERRO, por favor tente novamente');
      setFirstTrigger(false);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (!firstTrigger) {
      if (singleIsSimilar(speech, 'portal') || singleIsSimilar(speech, 'entrar')) {
        setFirstTrigger(true);
        handleSubmit();
      } else if (singleIsSimilar(speech, 'e-mail')) {
        (document.getElementsByName('email')[0]).focus();
      } else if (singleIsSimilar(speech, 'senha')) {
        (document.getElementsByName('senha')[0]).focus();
      } else {
        const element = document.activeElement;
        if (element.name === 'email') setEmail(speech);
        if (element.name === 'senha') setSenha(speech);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [speech]);

  return (
    <>
      <CardWhite>
        <div className="login__wrapper">
          <div className="login__header">
            <div className="row">
              <div className="col-12">
                <InstructionsWithVoice
                  text="Bem-vindo ao portal do CIAPD, digite seu e-mail e sua senha para
                entrar"
                />
              </div>
            </div>
          </div>
          <div className="login__body">
            <main>
              {!isLoading ? (
                <form
                  name="login-form"
                  aria-label="login"
                  onSubmit={e => {
                    e.preventDefault();
                    handleSubmit();
                  }}
                >
                  <div className="row">
                    <div className="col-12">
                      <div className="form__field">
                        <label
                          className="form__field__label"
                          htmlFor="login-email"
                        >
                          <span className="form__field__label__text">
                            E-mail:
                          </span>
                          <span className="form__field__helper">
                            seu e-mail cadastrado
                          </span>
                          <input
                            id="login-email"
                            className="form__field__input"
                            type="email"
                            name="email"
                            value={email}
                            onChange={e => {
                              setEmail(e.target.value);
                            }}
                            onFocus={() => {
                              setText('entre com seu e-mail cadastrado');
                            }}
                            required
                            aria-required="true"
                          />
                        </label>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12">
                      <div className="form__field">
                        <label
                          className="form__field__label"
                          htmlFor="login-senha"
                        >
                          <span className="form__field__label__text">
                            Senha:
                          </span>
                          <span className="form__field__helper">
                            sua senha secreta
                          </span>
                          <input
                            id="login-senha"
                            className="form__field__input"
                            type="password"
                            name="senha"
                            value={senha}
                            onChange={e => {
                              setSenha(e.target.value);
                            }}
                            onFocus={() => {
                              setText('entre com sua senha secreta');
                            }}
                            required
                            aria-required="true"
                          />
                        </label>
                      </div>
                    </div>
                  </div>
                  <div className="form__bottom">
                    <div className="row">
                      <div className="col-lg-12 d-flex justify-content-end align-items-center">
                        <ButtonWithVoice
                          customClass="form__button__next btn__white mr-1"
                          text={
                            faceLogin ? 'desligar câmera' : 'entrar com câmera'
                          }
                          action={toggleFace}
                        />
                        <ButtonWithVoice
                          customClass="form__button__next ml-1"
                          text="entrar no portal"
                          disabled={!email && !senha}
                          sub
                        />
                      </div>
                    </div>
                  </div>
                </form>
              ) : (
                <Loading />
              )}
            </main>
          </div>
        </div>
      </CardWhite>
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnVisibilityChange
        draggable
        pauseOnHover
      />
    </>
  );
};

export default LoginForm;
