import React, { useState, useEffect } from 'react';
import { Router } from '@reach/router';
import Login from '../login/Login';
import Navbar from './Navbar';
import Resume from '../resume/Resume';
import {
  contentRoutes,
  contentSingleRoutes
} from '../../app/utils/routes/index';
import Details from '../elements/Details';
import ContentList from '../elements/ContentList';
import VideoDetails from '../elements/VideoDetails';
import AudioPlayer from '../elements/AudioPlayer';
import { TextContext } from '../../app/providers/TextProvider';
import Footer from './Footer';
import GameDetails from '../elements/GameDetails';
import LoginService from '../../app/services/login/login-service';
import { WatsonContext } from '../../app/providers/WatsonProvider';

const Wrapper = () => {
  const [text, setText] = useState(null);
  const [watson, setWatson] = useState({
    voiceControl: true,
    voiceContext: true
  });

  useEffect(() => {
    LoginService.checkUser();
  });

  return (
    <WatsonContext.Provider value={{ watson, setWatson }}>
      <div className="svg__background">
        <TextContext.Provider value={{ text, setText }}>
          <AudioPlayer />
          <Router>
            <Login path="/login" />
            <Navbar path="/">
              <Resume path={contentRoutes.curriculo} />
              <ContentList path={contentRoutes.emprego} type="job" />
              <Details path={contentSingleRoutes.emprego} />
              <ContentList path={contentRoutes.cursos} type="course" />
              <Details path={contentSingleRoutes.cursos} />
              <ContentList path={contentRoutes.cultura} type="culture" />
              <Details path={contentSingleRoutes.cultura} />
              <ContentList path={contentRoutes.videoaulas} type="video" />
              <VideoDetails path={contentSingleRoutes.videoaulas} />
              <ContentList path={contentRoutes.jogos} type="game" />
              <GameDetails path={contentSingleRoutes.jogos} />
            </Navbar>
          </Router>
        </TextContext.Provider>
        {/* <div className="footer">
          <Footer />
        </div> */}
      </div>
    </WatsonContext.Provider>
  );
};

export default Wrapper;
