import React, { useState, useEffect, useContext } from 'react';
import { navigate } from '@reach/router';
import FocusLock from 'react-focus-lock';
import FirebaseService from '../../app/services/firebase/firebase-service';
import ViewResume from './ViewResume';
import EducationForm from './EducationForm';
import QualificationForm from './QualificationForm';
import ResumeInstructions from './ResumeInstructions';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import { SpeechContext } from '../../app/providers';
import { contentRoutes } from '../../app/utils/routes/index';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const Resume = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [hasResume, setHasResume] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [notLocked, setNotLocked] = useState(false);
  const speech = useContext(SpeechContext);

  const goToNext = () => {
    setCurrentStep(currentStep < 3 ? currentStep + 1 : 3);
  };

  const goToPrevious = () => {
    setCurrentStep(currentStep > 0 ? currentStep - 1 : 0);
  };

  useEffect(() => {
    const loadResume = async () => {
      try {
        const currentResume = await FirebaseService.getStudentResume();
        if (
          currentResume &&
          currentResume.education &&
          currentResume.education.length > 0
        ) {
          setHasResume(true);
        }
        if (
          currentResume &&
          currentResume.qualification &&
          currentResume.qualification.length > 0
        ) {
          setHasResume(true);
        }
      } catch (err) {
        // open toast
      } finally {
        setIsLoading(false);
      }
    };
    if (singleIsSimilar(speech, 'próximo') || singleIsSimilar(speech, 'continuar')) {
      goToNext();
    } else if (singleIsSimilar(speech, 'volta') || singleIsSimilar(speech, 'anterior')) {
      goToPrevious();
    }
    loadResume();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [speech]);


  const goToResume = step => {
    setCurrentStep(step);
  };

  const finishResume = () => {
    setNotLocked(true);
    navigate(contentRoutes.emprego);
  };

  return (
    <>
      {currentStep === 0 ? (
        <div className="resume__outter">
          <div className="container">
            <div className="row">
              <div className="col-12 d-flex justify-content-center align-items-center">
                <ResumeInstructions
                  currentStep={currentStep}
                  hasResume={hasResume}
                  goToResume={goToResume}
                  isLoading={isLoading}
                  fillResume={goToNext}
                />
              </div>
            </div>
          </div>
        </div>
      ) : (
          <FocusLock disabled={notLocked}>
            <div className="resume__outter">
              <div className="container">
                <div className="row">
                  <div className="col-12 d-flex justify-content-center align-items-center">
                    <ResumeInstructions
                      currentStep={currentStep}
                      hasResume={hasResume}
                      goToResume={goToResume}
                      isLoading={isLoading}
                    />
                    <QualificationForm
                      currentStep={currentStep}
                      goToNext={goToNext}
                    />
                    <EducationForm
                      currentStep={currentStep}
                      goToNext={goToNext}
                      goToPrevious={goToPrevious}
                    />
                    {currentStep === 3 && (
                      <ViewResume currentStep={currentStep} goToResume={goToResume} />
                    )}
                  </div>
                </div>
                <div className="resume__next">
                  <div className="row">
                    <div className="col-12 d-flex justify-content-end align-items-center">
                      <ButtonWithVoice
                        customClass="btn__white btn--margin"
                        text="finalizar currículo"
                        action={() => finishResume()}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </FocusLock>
        )}
    </>
  );
};

export default Resume;
