import stringSimilarity from 'string-similarity';
import { cleanSpeechFull, cleanSpeechStopwords, cleanSpeechSufix } from '../helpers/cleaners';

export const similarityFull = (words, wordsCompare) => {
  // console.time('similarity-full');
  const cleaned = cleanSpeechFull(words);
  let bestMatchGlobal = null;
  cleaned.forEach((word) => {
    const similarity = stringSimilarity.findBestMatch(word, wordsCompare);
    const { bestMatch } = similarity;
    if (bestMatch.rating >= 0.8) {
      bestMatchGlobal = similarity;
    }
  });
  // console.timeEnd('similarity-full');
  return bestMatchGlobal ? { found: bestMatchGlobal.bestMatch.target, index: bestMatchGlobal.bestMatchIndex } : bestMatchGlobal;
}

export const singleIsSimilar = (words, wordCompare) => {
  if (!words) {
    return false;
  }
  const cleanInput = cleanSpeechSufix(words);
  for (let i = 0; i < cleanInput.length; i += 1) {
    if (stringSimilarity.compareTwoStrings(cleanInput[i], wordCompare) >= 0.8) {
      return true;
    }
  }
  return false;
}

export const multipleIsSimilar = (words, wordCompare) => {
  const cleaned = cleanSpeechSufix(words);
  const target = cleanSpeechSufix(wordCompare).join('');
  const similarity = stringSimilarity.findBestMatch(target, cleaned);
  const { bestMatch } = similarity;

  return bestMatch.rating >= 0.8;
}

export const similaritySufix = (words, wordsCompare) => {
  // console.time('similarity-sufix');
  const cleaned = cleanSpeechSufix(words);
  const target = cleanSpeechSufix(wordsCompare);
  let bestMatchGlobal = null;
  cleaned.forEach((word) => {
    const similarity = stringSimilarity.findBestMatch(word, target);
    const { bestMatch } = similarity;
    if (bestMatch.rating >= 0.80) {
      bestMatchGlobal = similarity;
    }
  });
  // console.timeEnd('similarity-sufix');
  return bestMatchGlobal ? { found: bestMatchGlobal.bestMatch.target, index: bestMatchGlobal.bestMatchIndex } : bestMatchGlobal;
}

