export const getInnerHtmlById = id => {
  const element = document.querySelector(`#${id}`);
  return element && element.innerHTML;
};

export const getInnerHtmlByClass = name => {
  const elements = document.querySelectorAll(`.${name}`);
  return elements;
};

export const formatDate = date => {
  let month;
  const aux = date.split('/');

  switch (aux[1]) {
    case '01':
      month = 'janeiro';
      break;
    case '02':
      month = 'fevereiro';
      break;
    case '03':
      month = 'março';
      break;
    case '04':
      month = 'abril';
      break;
    case '05':
      month = 'maio';
      break;
    case '06':
      month = 'junho';
      break;
    case '07':
      month = 'julho';
      break;
    case '08':
      month = 'agosto';
      break;
    case '09':
      month = 'setembro';
      break;
    case '10':
      month = 'outubro';
      break;
    case '11':
      month = 'novembro';
      break;
    case '12':
      month = 'dezembro';
      break;
    default:
      return null;
  }

  return `dia ${aux[0]} de ${month} de ${aux[2]}.`;
};

export const stringToDigit = digit => {
  switch (digit) {
    case 'um':
      return '1';
    case 'dois':
      return '2';
    case 'três':
    case 'tres':
      return '3';
    case 'quatro':
      return '4';
    case 'cinco':
      return '5';
    case 'seis':
      return '6';
    case 'sete':
      return '7';
    case 'oito':
      return '8';
    case 'nove':
      return '9';
    default:
      return digit;
  }
};

export const formatPassword = password => {
  let words = password.split(' ');
  words = words.map(word => stringToDigit(word));
  return words.join('');
};

export const formatEmail = email => {
  const words = email.split(' ');
  let atIndex = -1;
  // eslint-disable-next-line no-restricted-syntax
  // for (const [i, word] of words.entries()) {
  //   if (buscaBR(word) === buscaBR('arroba')) {
  //     atIndex = i;
  //     break;
  //   }
  // }

  if (words[atIndex + 1] === 'g' && words[atIndex + 2] === 'meio') {
    words.splice(atIndex, words.length);
    words.push('@gmail.com');
  }

  const final = words.join('');

  return final;
};
