import React, { useState, useContext, useEffect } from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import { MoveFocusInside } from 'react-focus-lock';
import QualificationModal from './QualificationModal';
import CardWhite from '../elements/CardWhite';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import FirebaseService from '../../app/services/firebase/firebase-service';
import Loading from '../elements/Loading';
import { SpeechContext, TextContext } from '../../app/providers';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const QualificationForm = ({ currentStep, goToNext }) => {
  const [company, setCompany] = useState('');
  const [position, setPosition] = useState('');
  const [observation, setObservation] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const { setText } = useContext(TextContext);
  const speech = useContext(SpeechContext);

  const confirmQualification = () => {
    setCompany(company);
    setPosition(position);
    setObservation(observation);

    if (company && position) {
      setIsModalOpen(true);
    }
  };

  useEffect(() => {
    const element = document.activeElement;
    if (singleIsSimilar(speech, 'adicionar')) {
      if (company || position) {
        confirmQualification();
      }
    } else if (singleIsSimilar(speech, 'empresa')) {
      document.getElementsByName('company')[0].focus();
    } else if (singleIsSimilar(speech, 'cargo')) {
      document.getElementsByName('position')[0].focus();
    } else {
      if (element.name === 'company') setCompany(speech);
      if (element.name === 'position') setPosition(speech);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [speech]);

  const handleSubmit = async () => {
    setIsLoading(true);
    let message = '';
    try {
      if (company || position) {
        await FirebaseService.postResumeQualification({
          company,
          position
        });
        message = 'Experiência profissional adicionada com sucesso';
        setCompany('');
        setObservation('');
        setPosition('');
      }
    } catch (err) {
      message = 'Erro ao adicionar, por favor tente novamente';
    } finally {
      toast.info(message, {
        position: toast.POSITION.BOTTOM_RIGHT
      });
      goToNext();
      setText(message);
      setIsLoading(false);
      setIsModalOpen(false);
    }
  };

  return (
    <>
      {isModalOpen && (
        <QualificationModal
          isOpen={isModalOpen}
          onClose={() => {
            setIsModalOpen(false);
          }}
          company={company}
          position={position}
          observation={observation}
          addCallback={handleSubmit}
          cancelCallback={() => {
            setIsModalOpen(false);
          }}
        />
      )}
      {currentStep === 1 ? (
        <>
          <CardWhite minWidth>
            <div className="form__header">
              <div className="row">
                <div className="col-12">
                  <div className="form__header__title" role="none">
                    <h1>Experiência profissional: se você trabalhou, e o que fez</h1>
                  </div>
                </div>
              </div>
            </div>
            {isLoading ? (
              <Loading />
            ) : (
                <MoveFocusInside>
                  <form
                    name="qualification-form"
                    aria-label="experiência profissional"
                    onSubmit={e => {
                      e.preventDefault();
                      confirmQualification();
                    }}
                  >
                    <div className="form__body">
                      <div className="row">
                        <div className="col-6">
                          <div className="form__field">
                            <label
                              className="form__field__label"
                              htmlFor="qualification-company"
                            >
                              <span className="form__field__label__text">
                                Nome das empresas:
                            </span>
                              <span className="form__field__helper">
                                entre com os nomes dos lugares onde você trabalhou
                            </span>
                              <textarea
                                id="qualification-company"
                                className="form__field__input form__field__input--textarea"
                                type="text"
                                name="company"
                                value={company}
                                onChange={e => {
                                  setCompany(e.target.value);
                                }}
                                onFocus={() => {
                                  setText(
                                    'entre com os nomes das empresas em que você trabalhou'
                                  );
                                }}
                              />
                            </label>
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="form__field">
                            <label
                              className="form__field__label"
                              htmlFor="qualification-position"
                            >
                              <span className="form__field__label__text">
                                Nome dos cargos ou atividades que fez:
                            </span>
                              <span className="form__field__helper">
                                entre com os nomes dos cargos ou o que você fazia
                            </span>
                              <textarea
                                id="qualification-position"
                                className="form__field__input form__field__input--textarea"
                                type="text"
                                name="position"
                                value={position}
                                onChange={e => {
                                  setPosition(e.target.value);
                                }}
                                onFocus={() => {
                                  setText(
                                    'entre com os nomes dos cargos que ocupou nas empresas ou as atividades que fazia'
                                  );
                                }}
                              />
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form__bottom">
                      <div className="row">
                        <div className="col-lg-12 d-flex justify-content-end align-items-center">
                          <ButtonWithVoice
                            customClass={`form__button__next btn--green ${company && position ? 'pulse' : ''}`}
                            text="Estou pronto para continuar"
                            sub
                          />
                        </div>
                      </div>
                    </div>
                  </form>
                </MoveFocusInside>
              )}
          </CardWhite>
          <ToastContainer
            position="bottom-right"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnVisibilityChange
            draggable
            pauseOnHover
          />
        </>
      ) : null}
    </>
  );
};

export default QualificationForm;
