import React from 'react';
import ButtonWithVoice from '../voice/ButtonWithVoice';

const Qualification = ({ company, position, index, deleteQualification }) => {
  return (
    <div className="single__content">
      <div className="row">
        <div className="col-9 d-flex align-items-center justify-content-start">
          <div className="single__text">
            Eu trabalhei na empresa {company} como {position}.
          </div>
        </div>
        <div className="col-3 d-flex align-items-center justify-content-start">
          <div className="single__actions">
            <ButtonWithVoice
              customClass="btn__red"
              text="apagar"
              labelled={`Eu trabalhei na empresa ${company} como ${position}. Apagar`}
              action={() => {
                deleteQualification(index);
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Qualification;
