import React, { useState, useContext, useEffect } from 'react';
import LoginForm from './LoginForm';
import CameraCanvas from './CameraCanvas';
import { SpeechContext } from '../../app/providers';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const Login = () => {
  const [faceLogin, setFaceLogin] = useState(false);
  const [firstTrigger, setFirstTrigger] = useState(false);
  const speech = useContext(SpeechContext);

  const toggleFaceLogin = () => {
    setFaceLogin(!faceLogin);
  };

  useEffect(() => {
    if (!firstTrigger) {
      if (singleIsSimilar(speech, 'câmera') || singleIsSimilar(speech, 'face') || singleIsSimilar(speech, 'reconhecimento')) {
        setFirstTrigger(true);
        setFaceLogin(true);
      }
    } else if (singleIsSimilar(speech, 'desligar')) {
      setFirstTrigger(false);
      setFaceLogin(false);
    }
  }, [firstTrigger, speech, faceLogin]);

  const webcamOff = () => {
    toggleFaceLogin();
  };

  return (
    <>
      <div className="login__outter">
        <div className="row content__wrapper d-flex justify-content-center">
          <div className="col-6">
            <LoginForm toggleFace={toggleFaceLogin} faceLogin={faceLogin} />
          </div>
          {faceLogin ? (
            <div className="col-lg-6 ">
              <CameraCanvas turnOff={webcamOff} />
            </div>
          ) : null}
        </div>
      </div>
    </>
  );
};

export default Login;
