firebase.initializeApp(prodConfig);
export const auth = firebase.auth();
const firestore = firebase.firestore();

const getLoggedUser = () => LocalStorageService.getItem('loggedUser');

const FirebaseService = {
  // sign in method
  signIn: async (email, password) => {
    let response = {};
    try {
      response = await auth.signInWithEmailAndPassword(email, password);
    } catch (err) {
      response.err = err.code;
    }
    return response;
  },
  // sign out method
  signOut: () => auth.signOut,
  // on state change method
  onStateChange: async () => {
    auth.onAuthStateChanged(user => (user ? user.uid : null));
  },
  getUserDocument: async uid => {
    let response = {};
    if (!uid) return null;
    try {
      const snapshot = await firestore
        .collection('accounts')
        .doc(uid)
        .get();
      response = snapshot.data();
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  // get student resume by id
  getStudentResume: async () => {
    let response = {};
    const { uid } = getLoggedUser();
    try {
      const snapshot = await firestore
        .collection('resumes')
        .doc(uid)
        .get();
      response = snapshot.data();
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  // post
  postStudentResume: async (qualification, education) => {
    let response = {};
    const { uid, cid, name } = getLoggedUser();
    try {
      response = await firestore
        .collection('resumes')
        .doc(uid)
        .set({ qualification, education, name, cid, valid: false });
    } catch (err) {
      console.log(err);
      response.err = err;
    }
    return response;
  },
  // get content by type
  getContentByType: async type => {
    let response = {};
    try {
      const snapshot = await firestore.collection('contents').get();
      response = snapshot.docs
        .map(item => ({ id: item.id, data: item.data() }))
        .filter(item => item.data.type === type);
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  // get content by id (single one)
  getContentById: async uid => {
    let response = {};
    try {
      const snapshot = await firestore
        .collection('contents')
        .doc(uid)
        .get();
      response = snapshot || null;
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  // get listed students in facescan
  getWebcamStudents: async () => {
    let response = {};
    try {
      const snapshot = await firestore
        .collection('face-scan')
        .doc(registeredStudents)
        .get();
      response = snapshot.data().registered_students || null;
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  // get students id
  getStudentsToSearch: async () => {
    let response = {};
    try {
      const snapshot = await firestore
        .collection('face-scan')
        .doc(studentsMap)
        .get();
      response = snapshot.data() || null;
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  postResumeQualification: async qualification => {
    let response = {};
    const { uid, cid, name } = getLoggedUser();
    let currentResume = await FirebaseService.getStudentResume();
    // if it already exists
    if (currentResume && currentResume.qualification) {
      currentResume.qualification.push(qualification);
    } else {
      currentResume = { education: [], qualification: [qualification], name, cid, valid: false };
    }
    try {
      response = await firestore
        .collection('resumes')
        .doc(uid)
        .set(currentResume);
      return response;
    } catch (err) {
      response.err = err;
    }
    return response;
  },
  postResumeEducation: async education => {
    let response = {};
    const { uid, cid, name } = getLoggedUser();
    let currentResume = await FirebaseService.getStudentResume();
    // if the array already exists
    if (currentResume && currentResume.education) {
      currentResume.education.push(education);
    } else {
      currentResume = { education: [education], qualification: [], cid, name, valid: false };
    }
    try {
      response = await firestore
        .collection('resumes')
        .doc(uid)
        .set(currentResume);
      return response;
    } catch (err) {
      response.err = err;
    }
    return response;
  }
  // get listed students Float32Array for descriptions
  // getStudentsDescriptors: async () => {
  //   let response = {};
  //   try {
  //     const snapshot = await firestore
  //       .collection('face-scan')
  //       .doc(studentsDescriptors)
  //       .get();
  //     // transform back to Float32Array
  //     response = snapshot.data().registered_students || null;
  //   } catch (err) {
  //     response.err = err;
  //   }
  //   return response;
  // },
  // // save studendt descriptor Array
  // saveStudentDescriptors: async (label, descriptor) => {
  //   let response = {};
  //   // transform Float32Array before saving
  //   const myArray = Array.from(descriptor);
  //   try {
  //     const snapshot = await firestore
  //       .collection('face-scan')
  //       .doc(studentsDescriptors)
  //       .set({ label, descriptor: myArray });
  //     response = snapshot;
  //   } catch (err) {
  //     // console.log(err);
  //     response.err = err;
  //   }
  //   return response;
  // }
};

export default FirebaseService;
