import removeSufix from './sufix';
import removeStopWords from './stopwords';

export const cleanSpeechFull = (speech) => {
  // console.time('clean-full');
  let speechArray = speech.trim().split(' ');
  speechArray = removeStopWords(speechArray);
  speechArray = removeSufix(speechArray);
  // console.timeEnd('clean-full');
  return speechArray;
}

export const cleanSpeechStopwords = (speech) => {
  // console.time('clean-stopwords');
  let speechArray = speech.trim().split(' ');
  speechArray = removeStopWords(speechArray);
  // console.timeEnd('clean-stopwords');
  return speechArray;
}

export const cleanSpeechSufix = (speech) => {
  // console.time('clean-sufix');
  let speechArray = null;
  if (speech && speech) {
    speechArray = speech.trim().split(' ');
    speechArray = removeSufix(speechArray);
  }
  // console.timeEnd('clean-sufix');
  return speechArray;
}