import { useContext, useEffect } from 'react';
import { TextContext, PlaybackSpeedContext } from '../../app/providers/index';
import TextToSpeechService from '../../app/services/watson/text-service';
import { WatsonContext } from '../../app/providers/WatsonProvider';

const AudioPlayer = () => {
  const { text } = useContext(TextContext);
  const playbackSpeed = useContext(PlaybackSpeedContext);
  const { watson } = useContext(WatsonContext);

  const pauseAudios = () => {
    const audios = document.getElementsByTagName('audio');
    for (let i = 0; i < audios.length; i += 1) {
      audios[i].pause();
      audios[i].remove();
    }
  };

  useEffect(() => {
    pauseAudios();
    const playAudio = async stringToPlay => {
      if (stringToPlay) {
        try {
          const src = await TextToSpeechService.getSrcFromWatson(
            stringToPlay,
            watson.voiceContext
          );
          const audio = document.createElement('audio');
          audio.src = src;
          audio.playbackRate = playbackSpeed || 1;
          document.body.appendChild(audio);
          if (audio) {
            try {
              await audio.play();
            } catch (err) {
              // console.log(err);
            }
          }
        } catch (err) {
          // console.log(err);
        }
      }
    };

    if (text) {
      playAudio(text);
    }
  }, [text, playbackSpeed, watson]);

  return null;
};

export default AudioPlayer;
