import React from 'react';
import ButtonWithVoice from '../voice/ButtonWithVoice';

const Education = ({ institution, course, degree, index, deleteEducation }) => {
  return (
    <>
      <div className="single__wrapper">
        <div className="row">
          <div className="col-9 d-flex align-items-center justify-content-start">
            {degree ? (
              <div className="single__text">
                Eu completei o {degree} em {institution}.
              </div>
            ) : (
              <div className="single__text">
                Eu fiz o curso de {course} em {institution}.
              </div>
            )}
          </div>
          <div className="col-3 d-flex align-items-center justify-content-start">
            <div className="single__actions">
              <ButtonWithVoice
                customClass="btn__red"
                text="apagar"
                labelled={`Eu fiz o curso de ${course} em ${institution}. Apagar`}
                action={() => {
                  deleteEducation(index);
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Education;
