/* eslint-disable no-undef */
import React from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import { sounds } from '../../app/assets/sounds/links';
import {
  AuthProvider,
  MicContext,
  SpeechContext,
  PlaybackSpeedContext
} from '../../app/providers/index';
import Wrapper from './Wrapper';
import NavigationService from '../../app/services/navigation-service';
import SpeechToTextService from '../../app/services/watson/speech-service';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      micStatus: false,
      speechString: '',
      playbackSpeed: 1
    };
  }

  // async componentDidMount() {
  //   document.addEventListener('keydown', this.handleKeyPress);
  // }

  handleKeyPress = async e => {
    const { playbackSpeed } = this.state;
    const audioBeep = new Audio(sounds.micBeep);
    let input = '';
    switch (e.keyCode) {
      case 113:
        audioBeep.play();
        this.setState({ micStatus: true });
        input = await SpeechToTextService.getUserInput(true);
        if (input && input.data) {
          // console.log(input.data);

          // const cleanFull = cleanSpeechFull(input.data);
          // const cleanStopwords = cleanSpeechStopwords(input.data);
          // const cleanSufix = cleanSpeechSufix(input.data);

          // console.log('full: ', cleanFull);
          // console.log('stopwords: ', cleanStopwords);
          // console.log('sufix: ', cleanSufix);

          this.setState({ speechString: input.data });
          this.setState({ micStatus: false });
          NavigationService.mainContent(input.data);

        } else if (input && input.err) {
          toast.info('SERVIÇO DE VOZ NÃO DISPONÍVEL', {
            position: toast.POSITION.TOP_RIGHT
          });
        }
        break;
      case 118:
        if (playbackSpeed >= 0.5) {
          this.setState(prevState => ({
            playbackSpeed: prevState.playbackSpeed - 0.25
          }));
        }
        break;
      case 119:
        if (playbackSpeed >= 0.5) {
          this.setState(prevState => ({
            playbackSpeed: prevState.playbackSpeed - 0.25
          }));
        }
        break;
      default:
        break;
    }
    this.setState({ micStatus: false });
  };

  render() {
    const { micStatus, speechString, playbackSpeed } = this.state;

    return (
      <>
        <AuthProvider>
          <PlaybackSpeedContext.Provider value={playbackSpeed}>
            <SpeechContext.Provider value={speechString}>
              <MicContext.Provider value={micStatus}>
                <Wrapper />
              </MicContext.Provider>
            </SpeechContext.Provider>
          </PlaybackSpeedContext.Provider>
        </AuthProvider>
        <ToastContainer
          position="bottom-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnVisibilityChange
          draggable
          pauseOnHover
        />
      </>
    );
  }
}

export default App;
