import React, { useEffect, useState, useContext } from 'react';
import { navigate } from '@reach/router';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import {
  Card,
  CardActionArea,
  CardMedia,
  CardContent
} from '@material-ui/core';
import { getUrl } from '../../app/utils/imgur/index';
import { TextContext } from '../../app/providers';

const useStyles = makeStyles(
  createStyles({
    card: {
      maxWidth: 345,
      minWidth: 345,
      margin: 15,
      height: 420,
      background: '#f7f9fe'
    }
  })
);

const ContentSingle = ({ image, title, description, type, full }) => {
  const classes = useStyles();
  const [imageUrl, setImageUrl] = useState('');
  const { setText } = useContext(TextContext);

  useEffect(() => {
    if (image.endsWith('.png') || image.startsWith('https://')) {
      setImageUrl(image);
    } else {
      setImageUrl(getUrl(image).url);
    }
  }, [image, imageUrl]);

  const handleClick = () => {
    navigate(`${type}/${full.uid}`);
  };

  return (
    <>
      <div className="content__single">
        <Card className={classes.card} onClick={() => handleClick()}>
          {/* <CardActionArea onFocus={() => setText(`${title}, ${description}`)}> */}
          <CardActionArea onFocus={() => setText(`${title}`)}>
            {imageUrl && (
              <CardMedia
                component="img"
                height="220"
                key={imageUrl}
                alt={title}
                image={imageUrl}
                title={title}
              />
            )}
            <CardContent>
              <div className="content__single__title">
                <div className="row">
                  <div className="col-12">{title}</div>
                </div>
              </div>
              <div className="content__single__description">
                <div className="row">
                  <div className="col-12">{description}</div>
                </div>
              </div>
            </CardContent>
          </CardActionArea>
        </Card>
      </div>
    </>
  );
};

export default ContentSingle;
