import React, { useEffect, useContext } from 'react';
import Modal from 'react-modal';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import InstructionsWithVoice from '../voice/InstructionsWithVoice';
import TextToSpeechService from '../../app/services/watson/text-service';
import { SpeechContext } from '../../app/providers/SpeechProvider';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const customStyles = {
  overlay: {
    background: 'rgb(42, 52, 57, 0.90)'
  },
  content: {
    height: 'auto',
    width: '500px',
    top: '10%',
    left: '0',
    right: '0',
    bottom: 'auto',
    padding: '20px',
    margin: 'auto',
    background: '#f7f9fe',
    borderRadius: '5px',
    border: '0px'
  }
};

const EducationModal = ({
  isOpen,
  onClose,
  institution,
  course,
  cancelCallback,
  addCallback
}) => {
  const speech = useContext(SpeechContext);
  useEffect(() => {
    let canSubmit = true;
    Modal.setAppElement('#root');
    if (canSubmit) {
      if (singleIsSimilar(speech, 'sim')) {
        canSubmit = false;
        addCallback();
      } else if (singleIsSimilar(speech, 'cancelar')) {
        canSubmit = false;
        cancelCallback();
      }
    }
  });

  return (
    <>
      <Modal
        isOpen={isOpen}
        onRequestClose={onClose}
        style={customStyles}
        aria={{
          label: 'Confirmação de qualificação educacional',
          describedby: 'modalDescription'
        }}
      >
        <div className="modal__header">
          <div className="row m-0">
            <div className="col-12">
              <span id="modalHeading" className="modal__header__title">
                <InstructionsWithVoice
                  id="modalDescription"
                  text="Por favor confirme as informações para adicionar à seu currículo"
                  audio={`Por favor confirme as informações para adicionar à seu currículo.
                  ${TextToSpeechService.addPause(500)}
                  ${institution ? `Você estudou em ${institution}` : ', '}
                  ${institution ? `Você fez o curso de ${course}` : ''}`}
                />
              </span>
            </div>
          </div>
        </div>
        <div className="modal__body">
          <div className="row m-0">
            <div className="col-12">
              {institution && (
                <span className="modal__body__text">
                  Você estudou em {institution},
              </span>
              )}
              {course && (
                <span className="modal__body__text">
                  Você fez o curso de {course}?
              </span>
              )}
            </div>
          </div>
        </div>
        <div className="modal__actions d-flex justify-content-center align-items-center">
          <div className="row m-0">
            <div className="col-6">
              <ButtonWithVoice
                customClass="btn__white"
                action={cancelCallback}
                text="cancelar"
              />
            </div>
            <div className="col-6">
              <ButtonWithVoice
                customClass="form__button__next"
                action={addCallback}
                text="sim"
              />
            </div>
          </div>
        </div>
      </Modal>
    </>
  );
};

export default EducationModal;
