import { formatDate } from './text-helper-service';
import TextToSpeechServvice from './watson/text-service';

const ContentSingleService = {
  readSingleCard: card => {
    let stringToRead = '';
    if (card.type === 'job') {
      stringToRead += 'Cargo: ';
    }
    stringToRead += `${card.name}. ${TextToSpeechServvice.addPause(500)}${
      card.description
    }`;
    if (card.date) {
      const validDate = formatDate(card.date);
      if (card.type === 'job') {
        stringToRead += `.${TextToSpeechServvice.addPause(
          500
        )} Vaga válida até ${validDate}`;
      } else {
        stringToRead += `.${TextToSpeechServvice.addPause(
          500
        )} Acontecerá no dia ${validDate}`;
      }
    }
    return stringToRead;
  },
  readGame: game =>
    `${game.name}: ${TextToSpeechServvice.addPause(500)}${game.description}`,
  readVideo: video =>
    `${video.name}: ${TextToSpeechServvice.addPause(500)}${video.description}`
};

export default ContentSingleService;
