import * as faceapi from 'face-api.js';
import FirebaseService from '../firebase/firebase-service';

// private methods
const expressionLabelsMap = new Map([
  [1, 'happy'],
  [2, 'sad'],
  [3, 'surprised']
]);

const getRepresentativeExpression = expressions => {
  const array = Object.entries(expressions);
  let max = 0;
  let maxExpression = '';
  array.forEach(e => {
    if (e[1] > max) {
      [maxExpression, max] = e;
    }
  });
  return max > 0.8 ? maxExpression : null;
};

const matchExpression = (expressions, random) =>
  random === getRepresentativeExpression(expressions);

const getUserDescriptors = async (label, index) => {
  // get image from github
  const img = await faceapi.fetchImage(
    `https://raw.githubusercontent.com/ciapd/ciapd-faces/master/${label}/${index}.jpeg`
  );
  // get face detection for the image
  const detections = await faceapi
    .detectSingleFace(img, new faceapi.TinyFaceDetectorOptions())
    .withFaceLandmarks(true)
    .withFaceDescriptor();
  // return descriptor for the image
  // console.log(`label: ${label}, index: ${index} -> ${detections ? 'found' : 'not found'}`);
  return detections ? detections.descriptor : null;
};

export const registerUser = async (label, quantity) => {
  const promises = [];
  for (let i = 1; i <= quantity; i += 1) {
    promises.push(getUserDescriptors(label, i));
  }
  // start executing at the same time
  const descriptions = await Promise.all(promises);
  return new faceapi.LabeledFaceDescriptors(label, descriptions);
};

const loadLabeledStudents = async (labels, quantity) => {
  const response = await Promise.all(
    labels.map(async label => registerUser(label, quantity))
  );
  return response;
};

const getRandomExpression = random => expressionLabelsMap.get(random);

const startVideo = async (
  randomNumber,
  loadCallback,
  matchCallback,
  errorCallback
) => {
  // generate random expression
  const randomExpression = getRandomExpression(randomNumber);
  // console.time('load-and-create-students-label');
  // get registered students to loop github
  const students = await FirebaseService.getWebcamStudents();
  // get labels for all students
  const labeledDescriptors = await loadLabeledStudents(students, 5);
  // console.timeEnd('load-and-create-students-label');

  // cada descriptor eh um array de 128 floats32
  const maxDescriptorDistance = 0.6;

  const faceMatcher = new faceapi.FaceMatcher(
    labeledDescriptors,
    maxDescriptorDistance
  );

  let myInterval;

  loadCallback();
  const video = document.getElementById('inputVideo');
  if (video) {
    let faceCheck = false;
    let expressionCheck = false;
    const matchArray = [];
    if (navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices
        .getUserMedia({ video: true })
        .then(stream => {
          video.srcObject = stream;
        })
        .catch(() => {
          errorCallback();
        });
    }
    video.addEventListener('play', async () => {
      // create canvas to draw into
      const canvas = document.getElementById('inputCanvas');
      const displaySize = { width: 480, height: 360 };
      faceapi.matchDimensions(canvas, displaySize);

      myInterval = setInterval(async () => {
        // reset values, must be equal in the same frame
        faceCheck = false;
        expressionCheck = false;
        const detections = await faceapi
          .detectAllFaces(video, new faceapi.TinyFaceDetectorOptions())
          .withFaceLandmarks(true)
          .withFaceDescriptors()
          .withFaceExpressions();

        const resizedDetections = faceapi.resizeResults(
          detections,
          displaySize
        );

        if (
          resizedDetections &&
          resizedDetections.length > 0 &&
          resizedDetections[0].expressions
        ) {
          const results = resizedDetections.map(d =>
            faceMatcher.findBestMatch(d.descriptor)
          );

          // console.log(`label: ${results[0].label} | distance: ${results[0].distance}`);

          if (results[0].label !== 'unknown') {
            faceCheck = true;
            expressionCheck = matchExpression(
              resizedDetections[0].expressions,
              randomExpression
            );
          }

          if (faceCheck && expressionCheck) {
            matchCallback(results[0].label);
            clearInterval(myInterval);
          }

          matchArray.push(
            results.map(result => ({
              person: result.label,
              distance: result.distance
            }))
          );
        }
        // canvas.getContext('2d').clearRect(0, 0, canvas.width, canvas.height);
        // faceapi.draw.drawDetections(canvas, resizedDetections);
        // faceapi.draw.drawFaceExpressions(canvas, resizedDetections);
        // faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);
      }, 1000);
    });
  } else {
    errorCallback();
  }
};

const FaceScanService = {
  getRandomExpression: random => expressionLabelsMap.get(random),
  onCamera: async (
    randomNumber,
    loadCallback,
    matchCallback,
    errorCallback
  ) => {
    Promise.all([
      faceapi.loadSsdMobilenetv1Model('/models'),
      faceapi.loadFaceLandmarkTinyModel('/models'),
      faceapi.loadFaceRecognitionModel('/models'),
      faceapi.loadTinyFaceDetectorModel('/models'),
      faceapi.loadFaceExpressionModel('/models')
    ])
      .then(() => {
        startVideo(randomNumber, loadCallback, matchCallback, errorCallback);
      })
      .catch(() => {
        errorCallback();
      })
  }
};

export default FaceScanService;
