import React, { useContext } from 'react';
import { TextContext } from '../../app/providers';

const ButtonWithVoice = ({
  action,
  text,
  customClass,
  sub,
  customText,
  disabled,
  labelled
}) => {
  const { setText } = useContext(TextContext);

  const handleEvent = () => (customText ? setText(customText) : setText(text));

  return (
    <>
      {!labelled &&
        (!sub ? (
          <button
            className={customClass}
            type="button"
            onMouseEnter={handleEvent}
            onFocus={handleEvent}
            onClick={action}
            disabled={disabled || false}
          >
            {text}
          </button>
        ) : (
          <button
            className={customClass}
            type="submit"
            onMouseEnter={handleEvent}
            onFocus={handleEvent}
            onClick={action}
            disabled={disabled || false}
          >
            {text}
          </button>
        ))}
      {labelled && (
        <button
          aria-label={labelled}
          className={customClass}
          type="button"
          onMouseEnter={handleEvent}
          onFocus={handleEvent}
          onClick={action}
          disabled={disabled || false}
        >
          {text}
        </button>
      )}
    </>
  );
};

export default ButtonWithVoice;
