import { navigate } from '@reach/router';
import FirebaseService from '../firebase/firebase-service';
import { commonRoutes } from '../../utils/routes/index';
import LocalStorageService from '../local-storage-service';

const getUserDocument = async studentId => {
  let response;
  try {
    const loggedUser = await FirebaseService.getUserDocument(studentId);
    if (loggedUser) {
      LocalStorageService.setItem('loggedUser', loggedUser);
      response = true;
    } else {
      response = false;
    }
  } catch (err) {
    response = false;
  }
  return response;
};

const getLoggedUser = () => LocalStorageService.getItem('loggedUser');
const clearLoggedUser = () => LocalStorageService.removeItem('loggedUser');

const LoginService = {
  // if user is not logged in, redirect to login screen
  checkUser: () => {
    if (!getLoggedUser()) navigate(commonRoutes.login);
  },
  // log user using email and password
  loginUserWithEmail: async (email, password) => {
    // should hash password and save
    const response = await FirebaseService.signIn(email, password);
    if (!response.err && response && response.user && response.user.uid) {
      await getUserDocument(response.user.uid);
      const { voiceControl, voiceContext, name } = getLoggedUser();
      return { voiceControl, voiceContext, name };
    }
    return response;
  },
  loginUserWithDescriptor: async label => {
    // login using face descriptor
    const response = await FirebaseService.getStudentsToSearch();
    if (Object.prototype.hasOwnProperty.call(response, label)) {
      await getUserDocument(response[label]);
      const { voiceControl, voiceContext, name, } = getLoggedUser();
      return { voiceControl, voiceContext, name };
    }
    return response;
  },
  // log user out
  logoutUser: () => {
    clearLoggedUser();
    FirebaseService.signOut();
  },
  // get logged User
  getLoggedUser: () => {
    return LocalStorageService.getItem('loggedUser');
  }
};

export default LoginService;
