import React, { useContext } from 'react';
import { navigate } from '@reach/router';
import { contentRoutes } from '../../app/utils/routes/index';
import NavLink from './NavLink';
import { TextContext } from '../../app/providers/index';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import LoginService from '../../app/services/login/login-service';

const Navbar = ({ children }) => {
  const { setText } = useContext(TextContext);

  const logoutUser = async () => {
    try {
      setText(' ');
      await LoginService.logoutUser();
      navigate(`/login`);
    } catch (err) {
      // pop toast
    }
  };

  const readFocusedInnerHtml = () => {
    setText(document.activeElement.innerHTML);
  };

  return (
    <div>
      <nav
        className="nav__wrapper"
        role="navigation"
        aria-label="Navegação principal"
      >
        <NavLink
          onFocus={readFocusedInnerHtml}
          className="nav__item"
          to={contentRoutes.curriculo}
        >
          Curriculo
        </NavLink>
        <NavLink
          onFocus={readFocusedInnerHtml}
          className="nav__item"
          to={contentRoutes.emprego}
        >
          Empregos
        </NavLink>
        <NavLink
          onFocus={readFocusedInnerHtml}
          className="nav__item"
          to={contentRoutes.cursos}
        >
          Cursos
        </NavLink>
        <NavLink
          onFocus={readFocusedInnerHtml}
          className="nav__item"
          to={contentRoutes.cultura}
        >
          Cultura
        </NavLink>
        <NavLink
          onFocus={readFocusedInnerHtml}
          className="nav__item"
          to={contentRoutes.videoaulas}
        >
          Video aulas
        </NavLink>
        <NavLink
          onFocus={readFocusedInnerHtml}
          className="nav__item"
          to={contentRoutes.jogos}
        >
          Jogos
        </NavLink>
        <div className="footer__action__button-logout">
          <ButtonWithVoice
            customClass="btn__white no-margin"
            text="sair"
            customText="sair do portal"
            action={logoutUser}
          />
        </div>
      </nav>
      {children}
    </div>
  );
};

export default Navbar;
