import React, { useState, useEffect, useContext } from 'react';
import YouTubePlayer from 'react-player/lib/players/YouTube';
import { SpeechContext } from '../../app/providers';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const VideoWithVoice = ({ url, videoEnded }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const speech = useContext(SpeechContext);

  useEffect(() => {
    if (singleIsSimilar(speech, 'reproduzir')) {
      setIsPlaying(true);
    } else if (singleIsSimilar(speech, 'pausa')) {
      setIsPlaying(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [speech]);

  return (
    <YouTubePlayer
      url={url}
      playing={isPlaying}
      onEnded={videoEnded}
      controls
    />
  );
};

export default VideoWithVoice;
