/* eslint-disable no-nested-ternary */
import React, { useEffect, useState, useContext } from 'react';
import FirebaseService from '../../app/services/firebase/firebase-service';
import ContentListService from '../../app/services/content-list-service';
import ContentSingle from './ContentSingle';
import EmptyState from './EmptyState';
import Loading from './Loading';
import { TextContext, SpeechContext } from '../../app/providers';

const ContentList = ({ type }) => {
  const [isLoading, setIsLoading] = useState(true);
  const [data, setData] = useState([]);
  const { setText } = useContext(TextContext);
  const speech = useContext(SpeechContext);

  useEffect(() => {
    const loadContent = async () => {
      try {
        const content = await FirebaseService.getContentByType(type);
        setData(content);
        setText(ContentListService.readCards(content));
        if (content.length) {
          ContentListService.matchSpeechToNavigate(speech, content);
        }
      } catch (err) {
        // open customToast
      } finally {
        setIsLoading(false);
      }
    };

    loadContent();
    return () => { };
  }, [type, setText, speech]);

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
          <div className="content__wrapper">
            {data && data.length === 0 ? (
              <EmptyState />
            ) : data && data.length ? (
              data.map(card => (
                <ContentSingle
                  image={card.data.image}
                  title={card.data.name}
                  description={card.data.description}
                  url={card.data.url}
                  full={card.data}
                  key={card.id}
                  type={type}
                />
              ))
            ) : null}
          </div>
        )}
    </>
  );
};

export default ContentList;
