import { navigate } from '@reach/router';
import { singleIsSimilar } from './phonetic/custom/clean-similarity';

// private methods
// TODO tratar string para dar match com alg de fonetica
const cardNameMatch = (userInput, strings) => {
  let match;
  strings.forEach(string => {
    match = singleIsSimilar(userInput, string);
  });
  return (match);
};

const ContentListService = {
  // get card type
  getType: type => {
    switch (type) {
      case 'culture':
        return 'cultura';
      case 'job':
        return 'empregos';
      case 'course':
        return 'cursos';
      case 'video':
        return 'vídeo aulas';
      case 'game':
        return 'jogos';
      default:
        return 'inválido';
    }
  },
  // match user speech to navigate to card
  // TODO: tratar string com algs de fonetica
  matchSpeechToNavigate: (userInput, cardList) => {
    if (!userInput) return;
    let url = '';
    cardList.forEach(card => {
      if (cardNameMatch(userInput, [card.data.name])) {
        url = `${card.data.type}/${card.id}`;
      }
    });
    if (url) navigate(url);
  },

  readCards: cardList => {
    let stringToRead = '';
    if (cardList.length === 1) {
      stringToRead += 'Encontramos 1 resultado! ';
    } else if (cardList.length > 1) {
      stringToRead += `Encontramos ${cardList.length} resultados! `;
    }

    if (cardList && cardList.length > 0) {
      const arrayToRead = cardList.map(card => {
        return card.data.name;
      });
      stringToRead += arrayToRead.join(', ');
      stringToRead += '.';
    }
    return stringToRead;
  }
};

export default ContentListService;
