const tecnicSufix = [
  'ite',
  'oma',
  'ato',
  'eto',
  'ito',
  'ina',
  'ol',
  'ite',
  'ito',
  'ema',
  'io'
];

const religionSufix = ['ismo'];

const adverbalSufix = ['mente'];

const verbalSufix = [
  'ar',
  'ear',
  'entar',
  'ficar',
  'izar',
  'ejar',
  'ificar',
  'icar',
  'ilhar',
  'inhar',
  'iscar',
  'itar',
  'izar'
];

const substantiveFormationSufix = [
  'aco',
  'ado',
  'áceo',
  'ácea',
  'aico',
  'al',
  'ar',
  'ário',
  'ático',
  'az',
  'engo',
  'enho',
  'eno',
  'udo',
  'ento',
  'eo',
  'esco',
  'este',
  'estre',
  'ício',
  'ico',
  'il',
  'ino',
  'ivo',
  'onho',
  'oso'
];

const verbFormationSufix = [
  'ante',
  'ente',
  'inte',
  'ável',
  'ível',
  'io',
  'ivo',
  'tivo',
  'diço',
  'iço',
  'tício',
  'ício',
  'douro',
  'ouro',
  'ório',
  'tório'
];

const sufixArray = [
  ...new Set(
    verbFormationSufix
      // .concat(substantiveFormationSufix)
      .concat(adverbalSufix)
      .concat(actionSufix)
      .concat(religionSufix)
      .concat(verbalSufix)
      .concat(tecnicSufix)
      .concat(collectionSufix)
      .concat(personSufix)
  )
];

const cleanSufix = (word) => {
  let withoutSufix = word;
  for (let i = 0; i <= sufixArray.length; i += 1) {
    if (word.endsWith(sufixArray[i])) {
      withoutSufix = word.slice(0, (word.length - sufixArray[i].length));
      break;
    }
  }
  return withoutSufix;
}

const removeSufix = (words) => {
  const response = words.map((word) => {
    return cleanSufix(word);
  })
  return response;
}

export default removeSufix;
