import React from 'react';
import { Link } from '@reach/router';

const NavLink = props => {
  return (
    <Link
      className="custom__link"
      {...props}
      getProps={({ isCurrent }) => {
        // the object returned here is passed to the
        // anchor element's props
        return {
          style: {
            color: isCurrent ? '#ab1226' : '#2a3439'
          }
        };
      }}
    />
  );
};

export default NavLink;
