import { navigate } from '@reach/router';
import LoginService from './login/login-service';
import { contentRoutes } from '../utils/routes/index';
import { singleIsSimilar } from './phonetic/custom/clean-similarity';

// private
const transformStringToNavigation = string => {
  if (!string) return null;

  if (singleIsSimilar(string, 'cultura')) {
    return 'cultura';
  }
  if (singleIsSimilar(string, 'emprego') || singleIsSimilar(string, 'vaga')) {
    return 'emprego';
  }
  if (singleIsSimilar(string, 'curso')) {
    return 'curso';
  }
  if (singleIsSimilar(string, 'video') || singleIsSimilar(string, 'aula') || singleIsSimilar(string, 'vídeo')) {
    return 'video';
  }
  if (singleIsSimilar(string, 'jogo')) {
    return 'jogo';
  }
  if (singleIsSimilar(string, 'currículo') || singleIsSimilar(string, 'curriculo')) {
    return 'curriculo';
  }
  if (singleIsSimilar(string, 'sai')) {
    return 'sair';
  }
  if (singleIsSimilar(string, 'volta')) {
    return 'voltar';
  }
  return null;
};

const NavigationService = {
  mainContent: string => {
    if (!string) return;
    const aux = transformStringToNavigation(string);

    if (!aux) return;

    switch (aux) {
      case 'curriculo':
        navigate(`${contentRoutes.curriculo}`);
        break;
      case 'cultura':
        navigate(`${contentRoutes.cultura}`);
        break;
      case 'emprego':
        navigate(`${contentRoutes.emprego}`);
        break;
      case 'video':
        navigate(`${contentRoutes.videoaulas}`);
        break;
      case 'jogo':
        navigate(`${contentRoutes.jogos}`);
        break;
      case 'curso':
        navigate(`${contentRoutes.cursos}`);
        break;
      case 'sair':
        LoginService.logoutUser();
        break;
      case 'voltar':
        window.history.back();
        break;
      default:
    }
  },
  // go back to content list
  navigateBack: document => {
    if (document) {
      switch (document.type) {
        case 'job':
          navigate('/job');
          break;
        case 'culture':
          navigate('/culture');
          break;
        case 'course':
          navigate('/course');
          break;
        case 'video':
          navigate('/video');
          break;
        case 'game':
          navigate('/game');
          break;
        default:
          navigate('/');
          break;
      }
    }
  }
};

export default NavigationService;
