import React, { useEffect, useState, useContext } from 'react';
import CardWhite from './CardWhite';
import { getUrl } from '../../app/utils/imgur/index';
import { TextContext } from '../../app/providers';
import Loading from './Loading';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import FirebaseService from '../../app/services/firebase/firebase-service';
import ContentListService from '../../app/services/content-list-service';
import ContentSingleService from '../../app/services/content-single-service';
import NavigationService from '../../app/services/navigation-service';

const Details = ({ uid }) => {
  const [imageUrl, setImageUrl] = useState('');
  const [document, setDocument] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const { setText } = useContext(TextContext);

  useEffect(() => {
    const loadData = async () => {
      try {
        const data = await FirebaseService.getContentById(uid);
        if (data) {
          setDocument(data.data());
          if (data.data().image.endsWith('.png') || data.data().image.startsWith('https://')) {
            setImageUrl(data.data().image);
          } else {
            setImageUrl(getUrl(data.data().image).url);
          }
          setText(ContentSingleService.readSingleCard(data.data()));
        }
      } catch (err) {
        // pop toast
      } finally {
        setIsLoading(false);
      }
    };

    loadData(uid);
    return () => { };
  }, [uid, setText]);

  const goBack = () => {
    NavigationService.navigateBack(document);
  };

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
          <div className="row d-flex justify-content-center align-items-center m-0">
            <div className="col-8">
              <div className="wrapper">
                {document && (
                  <CardWhite>
                    <div className="row detail__header">
                      <div className="col-lg-9">
                        <div className="detail__header__title">
                          {document.name}
                        </div>
                      </div>
                      <div className="col-lg-3">
                        <div className="detail__header__date">
                          {document.date}
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="detail__body__image">
                          <img src={imageUrl} alt={document.title} width="300px" height="300px" />
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="detail__body__description">
                          {document.description}
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="detail__button__wrapper">
                          <ButtonWithVoice
                            customClass="form__button__next"
                            text="voltar"
                            customText={`Voltar para ${ContentListService.getType(
                              document.type
                            )}`}
                            action={goBack}
                          />
                        </div>
                      </div>
                    </div>
                  </CardWhite>
                )}
              </div>
            </div>
          </div>
        )}
    </>
  );
};

export default Details;
