/* eslint-disable no-undef */
import React, { useEffect, useState, useContext } from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import { navigate } from '@reach/router';
import CardWhite from '../elements/CardWhite';
import { translateExpression } from '../../app/pipes/expressionPipe';
import FaceScanService from '../../app/services/face-scan/face-scan';
import Loading from '../elements/Loading';
import LoginService from '../../app/services/login/login-service';
import { WatsonContext } from '../../app/providers/WatsonProvider';
import { contentRoutes } from '../../app/utils/routes/index';
import { TextContext } from '../../app/providers';
import InstructionsWithVoice from '../voice/InstructionsWithVoice';
import { getExpressionIcon } from '../../app/pipes/expressionIcon';

const CameraCanvas = ({ turnOff }) => {
  const { setText } = useContext(TextContext);
  const [expression, setExpression] = useState();
  const [isLoading, setIsLoading] = useState(true);
  const { setWatson } = useContext(WatsonContext);

  const loadCallback = () => {
    setIsLoading(false);
  };

  const matchCallback = async person => {
    try {
      setIsLoading(true);
      const response = await LoginService.loginUserWithDescriptor(person);
      if (response) {
        setText(`Olá, ${response.name}`);
        setWatson(response);
        navigate(contentRoutes.emprego);
      }
    } catch (err) {
      // pop toast
    } finally {
      setIsLoading(false);
      turnOff();
    }
  };

  const errorCallback = () => {
    toast.info('ERRO COM A WEBCAM', {
      position: toast.POSITION.TOP_RIGHT
    });
    setText('ERRO COM A WEBCAM, por favor tente novamente');
    turnOff();
  };

  useEffect(() => {
    let isSubscribed = true;
    const loadCamera = () => {
      if (isSubscribed) {
        const randomNumber = Math.floor(Math.random() * 3) + 1;
        setExpression(FaceScanService.getRandomExpression(randomNumber));
        FaceScanService.onCamera(
          randomNumber,
          loadCallback,
          matchCallback,
          errorCallback
        );
      }
    };
    loadCamera();
    return () => {
      isSubscribed = false;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <CardWhite>
        {isLoading ? (
          <Loading />
        ) : (
            <div className="camera__wrapper">
              <div className="row d-flex align-items-center justify-content-center">
                <div className="col-12 d-flex align-items-center justify-content-center">
                  <img src={getExpressionIcon(expression)} alt="" />
                </div>
              </div>
              <div className="row">
                <div className="col-12 d-flex justify-content-center align-items-center">
                  <div className="camera__expression">
                    <InstructionsWithVoice
                      text={`por favor tire os oculos e faça cara de ${translateExpression(
                        expression
                      )}`}
                    />
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-12 d-flex justify-content-center align-items-center">
                  <div className="camera__content">
                    <video
                      width="480"
                      height="360"
                      id="inputVideo"
                      autoPlay
                      muted
                    />
                    <canvas width="480" height="360" id="inputCanvas" />
                  </div>
                </div>
              </div>
            </div>
          )}
      </CardWhite>
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnVisibilityChange
        draggable
        pauseOnHover
      />
    </>
  );
};

export default CameraCanvas;
