import React, { useContext, useEffect } from 'react';
import CardWhite from '../elements/CardWhite';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import Loading from '../elements/Loading';
import InstructionsWithVoice from '../voice/InstructionsWithVoice';
import { SpeechContext } from '../../app/providers/SpeechProvider';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const ResumeInstructions = ({
  currentStep,
  hasResume,
  goToResume,
  isLoading,
  fillResume
}) => {
  const speech = useContext(SpeechContext);
  useEffect(() => {
    if (singleIsSimilar(speech, 'começar') || singleIsSimilar(speech, 'continuar')) {
      goToResume(1);
    } else if (singleIsSimilar(speech, 'visualizar') || singleIsSimilar(speech, 'currículo')) {
      goToResume(3);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [speech]);
  return (
    <>
      {currentStep === 0 && (
        <CardWhite>
          <div className="instructions__header">
            <div className="row">
              <div className="col-12">
                <h1>Instruções para preencher seu currículo</h1>
              </div>
            </div>
          </div>
          <div className="instructions__content">
            <div className="row">
              <div className="col-12">
                <InstructionsWithVoice
                  text="Aqui você irá preencher seu currículo para as empresas verem.
                  Se já trabalhou, diga onde e o que você fez. Em seguida, diga se já estudou, e o nome da escola."
                  ariaRole="region"
                  ariaLabel="instruções para preencher o currículo"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-12 d-flex justify-content-center align-items-center">
                <ButtonWithVoice
                  customClass="form__button__next"
                  text="estou pronto para começar"
                  action={fillResume}
                />
              </div>
            </div>
          </div>
          {isLoading && <Loading />}
          {hasResume && (
            <div className="instructions__actions">
              <div className="row">
                <div className="col-12">
                  <span id="viewResume">
                    <h2>
                      Você já cadastrou um currículo! Se quer visualizar, clique no botão abaixo.
                    </h2>
                  </span>
                </div>
              </div>
              <div className="row">
                <div className="col-12 d-flex align-items-center justify-content-center">
                  <div className="instructions__actions__button">
                    <ButtonWithVoice
                      customClass="form__button__next btn__white"
                      text="visualizar currículo"
                      aria-labelledby="viewResume"
                      action={() => {
                        goToResume(3);
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          )}
        </CardWhite>
      )}
    </>
  );
};

export default ResumeInstructions;
