import React, { useEffect, useContext } from 'react';
import { TextContext } from '../../app/providers/TextProvider';

const InstructionsWithVoice = ({ text, audio, ariaRole, ariaLive }) => {
  const { setText } = useContext(TextContext);

  useEffect(() => {
    const triggerAudio = () => (audio ? setText(audio) : setText(text));
    triggerAudio();
  }, [audio, setText, text]);

  return (
    <>
      {ariaLive ? (
        <span aria-labelledby={text} role={ariaRole} aria-live="polite">
          {text}
        </span>
      ) : (
        <span aria-labelledby={text} role={ariaRole}>
          {text}
        </span>
      )}
    </>
  );
};

export default InstructionsWithVoice;
