const happyIcon = "https://i.imgur.com/yP1i082.png";
const sadIcon = "https://i.imgur.com/rHw4Rg0.png";
const surprisedIcon = "https://i.imgur.com/6EWlX1p.png";

const expressions = new Map([
  ['happy', happyIcon],
  ['sad', sadIcon],
  ['surprised', surprisedIcon]
]);

export const getExpressionIcon = expression => expressions.get(expression);

export default getExpressionIcon;
