import React, { useState, useContext, useEffect } from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import { MoveFocusInside } from 'react-focus-lock';
import EducationModal from './EducationModal';
import CardWhite from '../elements/CardWhite';
import ButtonWithVoice from '../voice/ButtonWithVoice';
import FirebaseService from '../../app/services/firebase/firebase-service';
import Loading from '../elements/Loading';
import { SpeechContext, TextContext } from '../../app/providers';
import { singleIsSimilar } from '../../app/services/phonetic/custom/clean-similarity';

const EducationForm = ({ currentStep }) => {
  const [institution, setInstitution] = useState('');
  const [course, setCourse] = useState('');
  const [observation, setObservation] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const { setText } = useContext(TextContext);
  const speech = useContext(SpeechContext);

  const confirmEducation = () => {
    setCourse(course);
    setInstitution(institution);

    if (course || institution) {
      setIsModalOpen(true);
    }
  };

  useEffect(() => {
    const element = document.activeElement;
    if (singleIsSimilar(speech, 'adicionar')) {
      if (institution && course) {
        confirmEducation();
      }
    } else if (singleIsSimilar(speech, 'instituição')) {
      document.getElementsByName('institution')[0].focus();
    } else if (singleIsSimilar(speech, 'curso')) {
      document.getElementsByName('course')[0].focus();
    } else {
      if (element.name === 'institution') setInstitution(speech);
      if (element.name === 'course') setCourse(speech);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [speech]);
  const handleSubmit = async () => {
    setIsLoading(true);
    let message = '';
    try {
      if (institution || course) {
        await FirebaseService.postResumeEducation({
          institution,
          course
        });
        message = 'Experiência educacional adicionada com sucesso';
        setInstitution('');
        setCourse('');
        setObservation('');
      }
    } catch (err) {
      message = 'Erro ao adicionar, por favor tente novamente';
    } finally {
      toast.info(message, {
        position: toast.POSITION.BOTTOM_RIGHT
      });
      setText(message);
      setIsModalOpen(false);
      setIsLoading(false);
    }
  };

  return (
    <>
      {isModalOpen && (
        <EducationModal
          isOpen={isModalOpen}
          onClose={() => {
            setIsModalOpen(false);
          }}
          institution={institution}
          course={course}
          observation={observation}
          addCallback={handleSubmit}
          cancelCallback={() => {
            setIsModalOpen(false);
          }}
        />
      )}
      {currentStep === 2 ? (
        <>
          <CardWhite minWidth>
            <div className="form__header">
              <div className="row">
                <div className="col-12">
                  <div className="form__header__title">
                    <h1>Experiência educacional: onde estudou, e o que</h1>
                  </div>
                </div>
              </div>
            </div>
            {isLoading ? (
              <Loading />
            ) : (
                <MoveFocusInside>
                  <form
                    name="education-form"
                    aria-label="experiência educacional"
                    onSubmit={e => {
                      e.preventDefault();
                      confirmEducation();
                    }}
                  >
                    <div className="form__body">
                      <div className="row">
                        <div className="col-6">
                          <div className="form__field">
                            <label
                              className="form__field__label"
                              htmlFor="education-institution"
                            >
                              <span className="form__field__label__text">
                                Nome das escolas em que estudou:
                            </span>
                              <span className="form__field__helper">
                                o nome das escolas em que você estudou
                            </span>
                              <textarea
                                id="education-institution"
                                className="form__field__input form__field__input--textarea"
                                type="text"
                                name="institution"
                                value={institution}
                                onChange={e => {
                                  setInstitution(e.target.value);
                                }}
                                onFocus={() => {
                                  setText(
                                    'entre com o nome da escola em que você estudou'
                                  );
                                }}
                              />
                            </label>
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="form__field">
                            <label
                              className="form__field__label"
                              htmlFor="education-course"
                            >
                              <span className="form__field__label__text">
                                O nome de todos os cursos ou o que estudou:
                            </span>
                              <span className="form__field__helper">
                                nome dos cursos que você fez ou o que estudou
                            </span>
                              <textarea
                                id="education-course"
                                className="form__field__input form__field__input--textarea"
                                type="text"
                                name="course"
                                value={course}
                                onChange={e => {
                                  setCourse(e.target.value);
                                }}
                                onFocus={() => {
                                  setText(
                                    'entre com o nome dos cursos que você fez ou o que estudou'
                                  );
                                }}
                              />
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form__bottom">
                      <div className="row">
                        <div className="col-lg-12 d-flex justify-content-end align-items-center">
                          <ButtonWithVoice
                            customClass={`form__button__next btn--green ${institution && course ? 'pulse' : ''}`}
                            text="Estou pronto para continuar"
                            sub
                          />
                        </div>
                      </div>
                    </div>
                  </form>
                </MoveFocusInside>
              )}
          </CardWhite>
          <ToastContainer
            position="bottom-right"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnVisibilityChange
            draggable
            pauseOnHover
          />
        </>
      ) : null}
    </>
  );
};

export default EducationForm;
